(function(){
	'use strict';

	angular.module('app.main.lender.partner-details')
	.controller('BatchJobController',BatchJobController);

	BatchJobController.$inject = ['$stateParams','toastr','data', 'api'];


	function BatchJobController($stateParams, toastr, data, api){
		var batchJob = this;


		batchJob.getAllBatchJob = getAllBatchJob;
		batchJob.startBatchJob = startBatchJob;


		batchJob.jobsList = undefined;

		batchJob.getAllBatchJob();
		function getAllBatchJob(){
			data.get(api.getAllBatchJob,{partnerId: $stateParams.partnerId}, true)
			.then(function(response){
				console.log(response);
				if(response.data.batchJobs){
					batchJob.jobsList = response.data.batchJobs;
				}
			})
			.catch()
		}


		function startBatchJob(jobId){
			console.log(jobId);
			data.post(api.startBatchJob+'?jobId='+jobId,{jobId : jobId}, false)
			.then(function(response){
				console.log(response);
				if(response.data.jobStarted){
					toastr.success('Job Started', 'Success');
				}
			})
		}
	}
})();
(function(){
	'use strict';

	angular.module('app.main.lender.partner-details')
	.controller('UpdateKYCController',UpdateKYCController);


	UpdateKYCController.$inject = ['$mdDialog','data','api','application','partnerId'];
	function UpdateKYCController($mdDialog, data, api, application, partnerId){
		console.log(application);
		var kyc = this;

		kyc.getKycDetail = getKycDetail;
		kyc.cancel = cancel;
		kyc.saveKyc = saveKyc;

		kyc.kycData = undefined;


		kyc.getKycDetail();
		function getKycDetail(){
			data.get(api.getKycDetail, {custCode: application.custCode, partnerId : partnerId})
			.then(function(response){
				// console.log(response);
				kyc.kycData = response.data.applicant;
				console.log(kyc.kycData);
			})
			.catch();
		}

		function cancel(){
			$mdDialog.cancel();
		}

		function saveKyc(){
			data.post(api.editKycDetail,{applicant: kyc.kycData, loanApplicationId: application.id},false)
			.then(function(response){
				console.log(response);
				if(response.data.kycEdited){
					$mdDialog.hide({kycEdited : true});
				}
			})
			.catch()
		}
	}	
})();
(function(){
	'use strict';

	angular.module('app.main.lender.partner-details')
	.controller('RepaymentController',RepaymentController);

	RepaymentController.$inject=['$stateParams','toastr','api','data'];

	function RepaymentController($stateParams, toastr, api, data){
		var repayments = this;

		repayments.uploadFile = uploadFile;
		repayments.loading = false;
		repayments.bulkupdatedSuccess = false;
		repayments.progress = 0;

		function uploadFile(file){
			repayments.loading = true;
			data.upload(api.bulkRepayment, {'excel' : file, 'partnerId': $stateParams.partnerId}, false)
			.then(function(response){
				console.log(response);
				if(response.data.bulkupdatedSuccess){
					repayments.bulkupdatedSuccess = true;
					toastr.success('File Uploaded Successfully', 'Success');
				}else{
					toastr.error('Failure Uploading File', 'Failure');
				}
				repayments.loading = false;
			},function(error) {
				console.log(error);
			},function (evt) {
				repayments.progress = parseInt(100.0 * evt.loaded / evt.total);
				console.log('progress: ' + repayments.progress + '% ');
			})
			.catch();
		}

		
	}
})();
